import React from 'react';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import AuthorizationContainer from './Authorization/AuthorizationContainer';
import s from './Header.module.scss';

class HeaderContainer extends React.Component{
  render(){
    return (
      <header className={s.header}>
          <div className={s.header__body}>
          <div className={s.header__img}>
            <img className={s.headerImg} src="https://upload.wikimedia.org/wikipedia/commons/2/24/LEGO_logo.svg" alt="Logo" />
          </div>
          <div className={s.header__auth}>
            <AuthorizationContainer/>
          </div>
          </div>
          

          
      </header>
    )
  }
}



let HeaderAPIContainer = withRouter(HeaderContainer)
export default connect()(HeaderAPIContainer) 