import React from 'react';

const Form = (props) => {
    return (
        <form action="">
            <div>
                <input type="text" placeholder='Login' />
            </div>
            <div>
                <input type="password" placeholder='Password' />
            </div>
            <div>
                <input type="checkbox" placeholder='Checkbox' />
            </div>
            <button>
                Login
            </button>

            </form>
    )
}
export default Form