import {usersAPI} from '../api/api'

const FOLLOW = 'FOLLOW'
const UNFOLLOW = 'UNFOLLOW'
const SET_USERS = 'SET_USERS'
// const DEFAULT_AVATAR = 'https://upload.wikimedia.org/wikipedia/commons/thumb/8/88/Dmitry_Nagiev_2017_4.jpg/274px-Dmitry_Nagiev_2017_4.jpg'
const SET_CURRENT_PAGE = 'SET-CURRENT-PAGE'
const SET_TOTAL_USERS_COUNT = 'SET-TOTAL-USERS-COUNT'
const TOGGLE_IS_FETCHING = 'TOGGLE-IS-FETCHING'
const TOGGLE_IS_FOLLOWING_PROGRESS = 'TOGGLE-IS-FOLLOWING-PROGRESS'



let initialState = {
    users: [],
    count: 10, // Кол-во элементов (пользователей) на страницк
    totalUsersCount: 150, // Всего пользователей
    page: 1, // Номер активной страницы
    isFetching: false,
}

export const usersReducer = (state = initialState, action) => {

    switch (action.type) {
        case FOLLOW:
            return {
                ...state,
                users: state.users.map((el) => {
                    if (el.id === action.userId) {
                        return {
                            ...el,
                            followed: true
                        }
                    }
                    return el

                })
            }
            case UNFOLLOW:
                return {
                    ...state,
                    users: state.users.map((el) => {
                        if (el.id === action.userId) {
                            return {
                                ...el,
                                followed: false
                            }

                        }
                        return el
                    })
                }
                case SET_USERS:
                    return {
                        ...state,
                        users: [...action.users]
                    }
                    case SET_CURRENT_PAGE:
                        return {
                            ...state,
                            page: action.currentPage
                        }
                        case SET_TOTAL_USERS_COUNT:
                            return {
                                ...state,
                                totalUsersCount: action.totalNumber
                            }
                            case TOGGLE_IS_FETCHING:
                                return {
                                    ...state,
                                    isFetching: action.fetching
                                }

                                default:
                                    return state
    }
}

export const follow = (Id) => {
    return {
        type: FOLLOW,
        userId: Id
    }
}
export const unfollow = (Id) => {
    return {
        type: UNFOLLOW,
        userId: Id
    }
}
export const setUsers = (users) => {
    return {
        type: SET_USERS,
        users: users
    }
}
export const getCurrentPage = (currentPage) => {
    return {
        type: SET_CURRENT_PAGE,
        currentPage: currentPage
    }
}
export const setTotalUsersCount = (totalNumber) => {
    return {
        type: SET_TOTAL_USERS_COUNT,
        totalNumber: totalNumber
    }
}
export const toggleIsFetching = (fetching) => {
    return {
        type: TOGGLE_IS_FETCHING,
        fetching: fetching
    }
}

export const getUsersThunkCreator = (count, page) => {
    return (dispatch) => {
        dispatch(toggleIsFetching(true))
        usersAPI.getUsersApi(count, page).then(response => {
            dispatch(toggleIsFetching(false))
            dispatch(setUsers(response.data.items))
            dispatch(setTotalUsersCount(response.data.totalCount))
        })
    }
}

export const postUsersThunkCreator = (id) => {
    return (dispatch) => {
        usersAPI.postUsersApi(id).then(response => {
            if (response.data.resultCode === 0) {
                dispatch(follow(id))
            }
        })
    }
}

export const deleteUsersThunkCreator = (id) => {
    return (dispatch) => {
        usersAPI.deleteUsersApi(id).then(response => {
            if(response.data.resultCode === 0){
                dispatch(unfollow(id))
            }
        }) 
    }
}








export default usersReducer