import React from 'react';
import s from './Info.module.css'; 
import Status from './Status/Status';




const Info = (props) => {

    return (
      <div className={s.Info}>
        <div className={s.Info__User}>
          <div className={s.Info__img}>
            <img src={props.profile ? props.profile.photos.small : 'https://helpx.adobe.com/content/dam/help/en/stock/how-to/visual-reverse-image-search/jcr_content/main-pars/image/visual-reverse-image-search-v2_intro.jpg'} alt="Avatar" className={s.contentImg}/>
          </div>
          <div className={s.Info__name}>{props.fullName}</div>
        </div>
        <div className={s.Info__About}>{props.aboutMe}</div>
        <div className={s.Info__Contacts}>
        </div>
        <Status/>

       
      </div>
    )
}
export default Info