import React from 'react';
import DialogeMessage from './DialogeMessage/DialogeMessage';
import s from './DialogsMessages.module.css';
import NewMessageContainer from './NewMessage/NewMessageContainer';

class DialogsMessages extends React.Component{
    render(){
        return(
            <div className="DialogsMessages">
            <div className={s.dialogs__messages}>
                
               { this.props.messagesData.map((el) => ( <DialogeMessage text={el.text}/>)) } 
            </div>
            <NewMessageContainer />
        </div>
        )
    }
}

export default DialogsMessages