const ADD_MESSAGE = 'ADD-MESSAGE';
const CHANGE_MESSAGE_TEXT = 'CHANGE-MESSAGE-TEXT';

let initialState = {
    dialogsItems: {
        usersData: [{
                id: 1,
                name: 'Oleg'
            },
            {
                id: 2,
                name: 'Andrey'
            },
            {
                id: 3,
                name: 'Sveta'
            },
            {
                id: 4,
                name: 'Sasha'
            },
            {
                id: 5,
                name: 'Victor'
            },
            {
                id: 6,
                name: 'Valera'
            }
        ]
    },
    dialogsMessages: {
        messagesData: [{
                text: 'Hi!'
            },
            {
                text: 'How is it going?'
            },
            {
                text: 'Yo'
            }
        ],
        newMassageText: 'qwe'
    }
}

const dialogsReducer = (state = initialState, action) => {
    switch (action.type) {
        case ADD_MESSAGE:
            return {
                ...state,
                ...state.dialogsMessages.messagesData = [...state.dialogsMessages.messagesData, {text: state.dialogsMessages.newMassageText}],
                ...state.dialogsMessages.newMassageText = ''
            }
            case CHANGE_MESSAGE_TEXT:
            return{
                ...state,
                ...state.dialogsMessages.newMassageText = action.input
            }
                default:
                    return state
    }
}


export const addMessageActionCreator = () => ({
    type: ADD_MESSAGE
})
export const changeInputTextActionCreator = (text) => ({
    type: CHANGE_MESSAGE_TEXT,
    input: text
})




export default dialogsReducer