import React from 'react';
import { NavLink } from 'react-router-dom';
import s from './Menu.module.css';

const Menu = () => {
    return (
        <nav className={s.menu}>
            <div className={s.menu__block}>
                <NavLink to="/profile" activeClassName={s.active} className={s.menu__block_link}>Профиль</NavLink>
            </div>
            <div className={s.menu__block}>
                <NavLink to="/dialogs" activeClassName={s.active} className={s.menu__block_link}>Сообщения</NavLink>
            </div>
            <div className={s.menu__block}>
                <NavLink to="/users" activeClassName={s.active} className={s.menu__block_link}>Пользователи</NavLink>
            </div>
            <div className={s.menu__block}>
                <NavLink to="/news" activeClassName={s.active} className={s.menu__block_link}>Новости</NavLink>
            </div>
            <div className={s.menu__block}>
                <NavLink to="/Music" activeClassName={s.active} className={s.menu__block_link}>Музыка</NavLink>
            </div>
            <div className={s.menu__block}>
                <NavLink to="/setting" activeClassName={s.active} className={s.menu__block_link}>Настройки</NavLink>
            </div>
      </nav>
    )
}
export default Menu