const SET_USER_DATA = 'SET-USER-DATA'



let initialState = {
    id: null,
    email: null,
    login: null,
    isFetching: true,
    isAuth: false
}

const authReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_USER_DATA:
            return {
                ...state,
                ...action.data,
                isAuth: true
            }
            default:
                return state
    }
}

export const setUserData = (userId, email, login) => {
    return {
        type: SET_USER_DATA,
        data: {
            id: userId,
            email: email,
            login: login,
        }

    }
}



export default authReducer