import { profileAPI } from "../api/api";

const ADD_POST = 'ADD-POST';
const CHANGE_POST_TEXT = 'CHANGE-POST-TEXT';
const CHANGE_POST_AVATAR = 'CHANGE-POST-AVATAR';
const SET_USER_PROFILE = 'SET-USER-PROFILE';
const DELETE_POST = 'DELETE-POST';
// const DEFAULT_AVATAR = 'https://thumbs.dreamstime.com/b/default-avatar-profile-icon-vector-social-media-user-portrait-176256935.jpg'

let initialState = {
    posts: [{
        id: 1,
            avatar: 'https://i.imgur.com/vihivug.jpg',
            text: 'Hi'
        },
        {
            id: 2,
            avatar: 'https://i.imgur.com/vihivug.jpg',
            text: 'It is my second post!'
        },
    ],
    newMassageText: '',
    newAvatarSource: '',
    profile: null
}

const profileReducer = (state = initialState, action) => {

    switch (action.type) {
        case ADD_POST: {
            return {
                ...state,
                posts: [...state.posts, {avatar: state.newAvatarSource, text: state.newMassageText}],
                newAvatarSource: '',
                newMassageText: ''
            }
        }

        case CHANGE_POST_TEXT: {
            return {
                ...state,
                newMassageText: action.text
            }
        }
        case CHANGE_POST_AVATAR: {
            return {
                ...state,
                newAvatarSource: action.source
            }
        }
        case SET_USER_PROFILE:
            return{
                ...state,
                profile: action.profile
            }
            case DELETE_POST:
                return{
                    ...state,
                    posts: state.posts.filter(el => el.id !== action.postid)
                }
        default:
            return state
    }
}

export const addPostActionCreator = () => ({
    type: ADD_POST
})
export const deletePost = (postid) => ({
    type: DELETE_POST,
    postid

})
export const changePostActionCreator = (postText) => ({
    type: CHANGE_POST_TEXT,
    text: postText
})
export const changePostAvatarActionCreator = (postAvatarSource) => ({
    type: CHANGE_POST_AVATAR,
    source: postAvatarSource
})
export const setUserProfile = (profile) => ({
        type: SET_USER_PROFILE,
        profile: profile
})

export default profileReducer


export const getStatusThunkCreator = (status) => {
    return (dispatch) => {
        profileAPI.getStatus(status)
    }
}