import React from 'react'
import { connect } from 'react-redux';
import { getStatusThunkCreator } from '../../../../../redux/profile-reducer';
import s from './Status.module.scss'

class Status extends React.Component {
    componentDidMount(){
        this.props.getStatusThunkCreator(10)
    }
    state = {
        editMode: false,
        title: 'q'
    }
    activeMode = () => {
        console.log(this);
        this.setState( {editMode: true} )
    }
    deactivateMode = () => {
        console.log(this);
        this.setState( {editMode: false} )
    }
    
    render(){
        return(
            <div className={s.Status}>
                {this.state.editMode ? <input onBlur={ this.deactivateMode } className={s.Status__input} value={this.state.title} type="text" /> : <div onDoubleClick={ this.activeMode } className={s.Status__text}>{this.state.title}</div> }               

            </div>
        )
    }
}

const mapToStateProps = (state) => {
    return{}
}

export default connect(mapToStateProps, {getStatusThunkCreator})(Status)
