import React from 'react';
import s from './NewMessage.module.css';

class NewMessage extends React.Component {
  changeInputText = (e) => {
    let text = e.target.value
    this.props.changeInputTextContainer(text)
  }
  addInputText = () => {
    this.props.addInputTextContainer()
  }
  render() {
    return (
      <div className={s.NewPost}>
        <textarea onChange={this.changeInputText} className={s.NewPost__area} placeholder='Write something...' value={this.props.newMassageText}></textarea>
        <button onClick={this.addInputText} className={s.NewPost__btn}>Add</button>
      </div>
    )
  }
}

export default NewMessage