import React from 'react';
import s from './NewPost.module.css';


const NewPost = (props) => {
  const updateAreaText = (e) => {
    let text = e.target.value;
    props.containerUpdateAreaText(text)
  }
  const addPost = () => {
    props.containerAddPost()
  }
  const updateAvatar = (e) => {
    let source = e.target.value;
    props.containerUpdateAvatar(source)
  }
  return (
    <div className={s.NewPost}>
      <input onChange={updateAreaText} className={s.NewPost__area} newMassageText={props.newMassageText} type="text"></input>
      <button onClick={addPost} className={s.NewPost__btn}>Add</button>
      <input onChange={updateAvatar} className={s.NewPost_input} newAvatarSource={props.newAvatarSource} type="text" />
    </div>
  )
}
export default NewPost