import React from 'react';
import s from './Post.module.css'; 

const Post = (props) => {
    return (
      <div className={s.content__post}>
      <div className={s.content__postImg}>
        <img src={props.avatar} alt="Avatar" />
      </div>
      <div className={s.content__postText}>{props.message}</div>
    </div>
    )
}
export default Post