import { connect } from 'react-redux';
import { addPostActionCreator, changePostActionCreator, changePostAvatarActionCreator } from '../../../../redux/profile-reducer';
import NewPost from './NewPost';

let mapStoreToProps = (state) => {
    return {
        newMassageText: state.profile.newMassageText,
        newAvatarSource: state.profile.newAvatarSource
    }
}
let mapDispatchToProps = (dispatch) => {
    return {
        containerUpdateAreaText: (text) => {
            dispatch(changePostActionCreator(text))
        },
        containerAddPost: () => {
            dispatch(addPostActionCreator())
        },
        containerUpdateAvatar: (source) => {
            dispatch(changePostAvatarActionCreator(source))
        }
    }
}
const NewPostContainer = connect(mapStoreToProps, mapDispatchToProps)(NewPost)

export default NewPostContainer