import { connect } from 'react-redux';
import { addMessageActionCreator, changeInputTextActionCreator } from '../../../../redux/dialogs-reducer'
import NewMessage from './NewMessage';

let mapStateToProps = (state) =>{
  return {
    newMassageText: state.dialogs.dialogsMessages.newMassageText
  }
}
let mapDispatchToProps = (dispatch) =>{
  return {
    changeInputTextContainer: (text) => {
      dispatch(changeInputTextActionCreator(text))
    },
    addInputTextContainer: () => {
      dispatch(addMessageActionCreator())
    }
  }
}


export default connect(mapStateToProps, mapDispatchToProps)(NewMessage);
